<style>
  /*不显示竖向Menu的边栏*/
  .ivu-menu-vertical.ivu-menu-light:after {
    width: 0px;
  }

  .leftPanel {
    height: 100%;
  }

  .rightPanel {
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column;
    flex-flow: column;
    /*padding: 10px;*/
  }
  .record-back-to-list {
    position: absolute;
    top: 3px;
    right: 3px;
    cursor: pointer;
    height: 33px;
    width: 72px;
    background: url('../../../assets/content/button_back_default.png') no-repeat;
    background-size: 100% 100%;
  }
  .record-back-to-list:hover {
    background: url('../../../assets/content/button_back_active.png') no-repeat;
    background-size: 100% 100%;
  }
</style>
<template>
  <Row class="fullWidth">
    <iCol span="1" class="leftPanel left-background">
      <Row style="padding: 8px;" class="left-top">
        <span style="font-size: 16px;">{{ currentOrgName }}</span>
      </Row>
      <Menu activeName="task" width="auto" class="left-menu" :open-names="['device_event', 'tranceiver_event']" @on-select="onNaviMenuClick">
        <Submenu name="device_event">
          <template slot="title">
            <span>设备事件</span>
            <img src="../../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="device_unconfirmed">未确认事件</Menu-item>
          <Menu-item name="device_confirmed">已确认事件</Menu-item>
        </Submenu>
        <Submenu name="tranceiver_event">
          <template slot="title">
            <span>传输装置事件</span>
            <img src="../../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="tranceiver_unconfirmed">未确认事件</Menu-item>
          <Menu-item name="tranceiver_confirmed">已确认事件</Menu-item>
        </Submenu>
      </Menu>
    </iCol>
    <iCol class="rightPanel">
      <router-view></router-view>
    </iCol>
  </Row>
</template>

<script>
  import config from '../../../config/config'

  export default {
    data() {
      return {
        orgName: '星海假日酒店',
        currentOrgName: undefined
      };
    },
    methods: {
      fetchData: function () {
        document.title = '单位管理 - ' + config.web_name;
        this.$store.commit('setActiveNaviMenu', 'superAdmin');
      },
      backToWelcome: function () {
        this.$router.push("/welcome");
      },
      onNaviMenuClick: function (key) {
        //Route to sub module
        this.$router.push(key);
      }
    },
    mounted() {
      let page = this.topYunSession('route_list');
      this.currentOrgName = page.mcBuildingId === undefined ? localStorage.current_org_name : page.mcBuildingName;
      this.fetchData();
    }

  }
</script>



